nav{
    background: rgba(239,238,253,0.8);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var( --color-primary-variant);
    font-size: 1.1rem;
}

nav a:hover{
    background-color: rgba(250, 250, 250);
    color: var( --color-primary-variant);
}

nav a.active{
    background: var(--color-bg);
}